import React, { Component } from "react";
import Navbar from "../components/layouts/navbar";
import {
  Accordion,
  AccordionItem,
  AccordionItemHeading,
  AccordionItemPanel,
  AccordionItemButton
} from "react-accessible-accordion";
import "react-accessible-accordion/dist/fancy-example.css";

class FAQ extends Component {
  question_and_answer = [
    {
      question: "What?",
      answer: "Hello World"
    },
    {
      question: "What kinds of dogs are there?",
      answer: "Hello World"
    },
    {
      question: "How do you acquire a dog?",
      answer: "Hello World"
    }
  ];

  constructor() {
    super();
    this.state = {
      showConent: true
    };
  }

  fn_toggle = () => {
    this.setState({ showConent: !this.state.showConent });
  };

  render() {
    const { showConent } = this.state;
    return (
      <React.Fragment>
        <Navbar />
        <div className="ui text container">
          <center>
            <h1 className="warranty">FAQ</h1>
            <br />
          </center>
          <Accordion>
            {this.question_and_answer.map((data, key) => {
              return (
                <AccordionItem key={key}>
                  <AccordionItemHeading>
                    <AccordionItemButton>{data.question}</AccordionItemButton>
                  </AccordionItemHeading>
                  <AccordionItemPanel>
                    <p>{data.answer}</p>
                  </AccordionItemPanel>
                </AccordionItem>
              );
            })}
          </Accordion>
        </div>
      </React.Fragment>
    );
  }
}

export default FAQ;
